import Vue from 'vue';
import i18n from '../../../i18n';
import { apiUrl } from '../../../config';
import getMD5 from '../../scripts/get-md5';
export default Vue.extend({
    i18n: i18n('common/views/components/uploader.vue'),
    data() {
        return {
            uploads: []
        };
    },
    methods: {
        checkExistence(fileData) {
            return new Promise((resolve, reject) => {
                const data = new FormData();
                data.append('md5', getMD5(fileData));
                this.$root.api('drive/files/find-by-hash', {
                    md5: getMD5(fileData)
                }).then(resp => {
                    resolve(resp.length > 0 ? resp[0] : null);
                });
            });
        },
        upload(file, folder, name) {
            if (folder && typeof folder == 'object')
                folder = folder.id;
            const id = Math.random();
            const reader = new FileReader();
            reader.onload = (e) => {
                this.checkExistence(e.target.result).then(result => {
                    if (result !== null) {
                        this.$emit('uploaded', result);
                        return;
                    }
                    const ctx = {
                        id: id,
                        name: name || file.name || 'untitled',
                        progress: undefined,
                        img: window.URL.createObjectURL(file)
                    };
                    this.uploads.push(ctx);
                    this.$emit('change', this.uploads);
                    const data = new FormData();
                    data.append('i', this.$store.state.i.token);
                    data.append('force', 'true');
                    data.append('file', file);
                    if (folder)
                        data.append('folderId', folder);
                    if (name)
                        data.append('name', name);
                    const xhr = new XMLHttpRequest();
                    xhr.open('POST', apiUrl + '/drive/files/create', true);
                    xhr.onload = (e) => {
                        const driveFile = JSON.parse(e.target.response);
                        this.$emit('uploaded', driveFile);
                        this.uploads = this.uploads.filter(x => x.id != id);
                        this.$emit('change', this.uploads);
                    };
                    xhr.upload.onprogress = e => {
                        if (e.lengthComputable) {
                            if (ctx.progress == undefined)
                                ctx.progress = {};
                            ctx.progress.max = e.total;
                            ctx.progress.value = e.loaded;
                        }
                    };
                    xhr.send(data);
                });
            };
            reader.readAsArrayBuffer(file);
        }
    }
});
