import Vue from 'vue';
export default Vue.extend({
    model: {
        prop: 'model',
        event: 'change'
    },
    props: {
        model: {
            required: false
        },
        value: {
            required: false
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        checked() {
            return this.model === this.value;
        }
    },
    methods: {
        toggle() {
            this.$emit('change', this.value);
        }
    }
});
