import Vue from 'vue';
import { getStaticImageUrl } from '../../../common/scripts/get-static-image-url';
import { twemojiSvgBase } from '../../../../../misc/twemoji-base';
export default Vue.extend({
    props: {
        name: {
            type: String,
            required: false
        },
        emoji: {
            type: String,
            required: false
        },
        normal: {
            type: Boolean,
            required: false,
            default: false
        },
        noStyle: {
            type: Boolean,
            required: false,
            default: false
        },
        customEmojis: {
            required: false,
            default: () => []
        },
        isReaction: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            url: null,
            char: null,
            customEmoji: null,
        };
    },
    computed: {
        alt() {
            return this.customEmoji ? `:${this.customEmoji.name}:` : this.char;
        },
        useOsNativeEmojis() {
            return this.$store.state.device.useOsDefaultEmojis && !this.isReaction;
        },
    },
    created() {
        this.$root.getMeta().then(meta => {
            let ce = [];
            if (this.customEmojis)
                ce = ce.concat(this.customEmojis);
            if (meta && meta.emojis)
                ce = ce.concat(meta.emojis);
            if (this.name) {
                const customEmoji = ce.find(x => x.name == this.name);
                if (customEmoji) {
                    this.customEmoji = customEmoji;
                    this.url = this.$store.state.device.disableShowingAnimatedImages
                        ? getStaticImageUrl(customEmoji.url)
                        : customEmoji.url;
                }
            }
        });
        if (!this.name) {
            this.char = this.emoji;
        }
        if (this.char) {
            let codes = Array.from(this.char).map(x => x.codePointAt(0).toString(16));
            if (!codes.includes('200d'))
                codes = codes.filter(x => x != 'fe0f');
            codes = codes.filter(x => x && x.length);
            this.url = `${twemojiSvgBase}/${codes.join('-')}.svg`;
        }
    },
});
