import Vue from 'vue';
export default Vue.extend({
    inject: {
        horizonGrouped: {
            default: false
        }
    },
    props: {
        type: {
            type: String,
            required: false
        },
        primary: {
            type: Boolean,
            required: false,
            default: false
        },
        inline: {
            type: Boolean,
            required: false,
            default() {
                return this.horizonGrouped;
            }
        },
        link: {
            type: Boolean,
            required: false,
            default: false
        },
        autofocus: {
            type: Boolean,
            required: false,
            default: false
        },
        wait: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    mounted() {
        if (this.autofocus) {
            this.$nextTick(() => {
                this.$el.focus();
            });
        }
    },
    methods: {
        onMousedown(e) {
            function distance(p, q) {
                return Math.hypot(p.x - q.x, p.y - q.y);
            }
            function calcCircleScale(boxW, boxH, circleCenterX, circleCenterY) {
                const origin = { x: circleCenterX, y: circleCenterY };
                const dist1 = distance({ x: 0, y: 0 }, origin);
                const dist2 = distance({ x: boxW, y: 0 }, origin);
                const dist3 = distance({ x: 0, y: boxH }, origin);
                const dist4 = distance({ x: boxW, y: boxH }, origin);
                return Math.max(dist1, dist2, dist3, dist4) * 2;
            }
            const rect = e.target.getBoundingClientRect();
            const ripple = document.createElement('div');
            ripple.style.top = (e.clientY - rect.top - 1).toString() + 'px';
            ripple.style.left = (e.clientX - rect.left - 1).toString() + 'px';
            this.$refs.ripples.appendChild(ripple);
            const circleCenterX = e.clientX - rect.left;
            const circleCenterY = e.clientY - rect.top;
            const scale = calcCircleScale(e.target.clientWidth, e.target.clientHeight, circleCenterX, circleCenterY);
            setTimeout(() => {
                ripple.style.transform = 'scale(' + (scale / 2) + ')';
            }, 1);
            setTimeout(() => {
                ripple.style.transition = 'all 1s ease';
                ripple.style.opacity = '0';
            }, 1000);
            setTimeout(() => {
                if (this.$refs.ripples)
                    this.$refs.ripples.removeChild(ripple);
            }, 2000);
        }
    }
});
