import Vue from 'vue';
export default Vue.extend({
    provide: {
        horizonGrouped: true
    },
    props: {
        inputs: {
            type: Boolean,
            required: false,
            default: false
        },
        noGrow: {
            type: Boolean,
            required: false,
            default: false
        }
    },
    data() {
        return {
            children: 0
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.children = this.$slots.default.length;
        });
    }
});
