import { concat, sum } from './array';
export function createLeaf(node) {
    return { node, children: [] };
}
export function createTree(node, children) {
    return { node, children };
}
export function hasChildren(t) {
    return t.children.length !== 0;
}
export function preorder(t) {
    return [t.node, ...preorderF(t.children)];
}
export function preorderF(ts) {
    return concat(ts.map(preorder));
}
export function countNodes(t) {
    return preorder(t).length;
}
export function countNodesF(ts) {
    return sum(ts.map(countNodes));
}
