import Vue from 'vue';
export default Vue.extend({
    model: {
        prop: 'value',
        event: 'change'
    },
    props: {
        value: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        checked() {
            return this.value;
        }
    },
    methods: {
        toggle() {
            if (this.disabled)
                return;
            this.$emit('change', !this.checked);
        }
    }
});
