import Vue from 'vue';
import i18n from '../../../i18n';
import { url as local, lang } from '../../../config';
export default Vue.extend({
    i18n: i18n('common/views/components/url-preview.vue'),
    props: {
        url: {
            type: String,
            require: true
        },
        detail: {
            type: Boolean,
            required: false,
            default: false
        },
        compact: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    inject: {
        narrow: {
            default: false
        }
    },
    data() {
        const isSelf = this.url.startsWith(local);
        const hasRoute = (this.url.substr(local.length) === '/') ||
            this.url.substr(local.length).startsWith('/@') ||
            this.url.substr(local.length).startsWith('/notes/') ||
            this.url.substr(local.length).startsWith('/tags/') ||
            this.url.substr(local.length).startsWith('/pages/');
        return {
            local,
            fetching: true,
            title: null,
            description: null,
            thumbnail: null,
            icon: null,
            sitename: null,
            player: {
                url: null,
                width: null,
                height: null
            },
            tweetUrl: null,
            playerEnabled: false,
            self: isSelf,
            hasRoute: hasRoute,
            attr: hasRoute ? 'to' : 'href',
            target: hasRoute ? null : '_blank'
        };
    },
    created() {
        const requestUrl = new URL(this.url);
        if (this.detail && requestUrl.hostname == 'twitter.com' && /^\/.+\/status(es)?\/\d+/.test(requestUrl.pathname)) {
            this.tweetUrl = requestUrl;
            const twttr = window.twttr || {};
            const loadTweet = () => twttr.widgets.load(this.$refs.tweet);
            if (twttr.widgets) {
                Vue.nextTick(loadTweet);
            }
            else {
                const wjsId = 'twitter-wjs';
                if (!document.getElementById(wjsId)) {
                    const head = document.getElementsByTagName('head')[0];
                    const script = document.createElement('script');
                    script.setAttribute('id', wjsId);
                    script.setAttribute('src', 'https://platform.twitter.com/widgets.js');
                    head.appendChild(script);
                }
                twttr.ready = loadTweet;
                window.twttr = twttr;
            }
            return;
        }
        if (requestUrl.hostname === 'music.youtube.com') {
            requestUrl.hostname = 'youtube.com';
        }
        const requestLang = (lang || 'ja-JP').replace('ja-KS', 'ja-JP');
        requestUrl.hash = '';
        fetch(`/url?url=${encodeURIComponent(requestUrl.href)}&lang=${requestLang}`).then(res => {
            res.json().then(info => {
                if (info.url == null)
                    return;
                this.title = info.title;
                this.description = info.description;
                this.thumbnail = info.thumbnail;
                this.icon = info.icon;
                this.sitename = info.sitename;
                this.fetching = false;
                this.player = info.player;
            });
        });
    }
});
