import anime from 'animejs';
import define from '../../../common/define-widget';
import i18n from '../../../i18n';
export default define({
    name: 'slideshow',
    props: () => ({
        folder: undefined,
        size: 0
    })
}).extend({
    i18n: i18n('common/views/widgets/slideshow.vue'),
    data() {
        return {
            images: [],
            fetching: true,
            clock: null
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.applySize();
        });
        if (this.props.folder !== undefined) {
            this.fetch();
        }
        this.clock = setInterval(this.change, 10000);
    },
    beforeDestroy() {
        clearInterval(this.clock);
    },
    methods: {
        func() {
            this.resize();
        },
        applySize() {
            let h;
            if (this.props.size == 1) {
                h = 250;
            }
            else {
                h = 170;
            }
            this.$el.style.height = `${h}px`;
        },
        resize() {
            if (this.props.size == 1) {
                this.props.size = 0;
            }
            else {
                this.props.size++;
            }
            this.save();
            this.applySize();
        },
        change() {
            if (this.images.length == 0)
                return;
            const index = Math.floor(Math.random() * this.images.length);
            const img = `url(${this.images[index].url})`;
            this.$refs.slideB.style.backgroundImage = img;
            anime({
                targets: this.$refs.slideB,
                opacity: 1,
                duration: 1000,
                easing: 'linear',
                complete: () => {
                    // 既にこのウィジェットがunmountされていたら要素がない
                    if (this.$refs.slideA == null)
                        return;
                    this.$refs.slideA.style.backgroundImage = img;
                    anime({
                        targets: this.$refs.slideB,
                        opacity: 0,
                        duration: 0
                    });
                }
            });
        },
        fetch() {
            this.fetching = true;
            this.$root.api('drive/files', {
                folderId: this.props.folder,
                type: 'image/*',
                limit: 100
            }).then(images => {
                this.images = images;
                this.fetching = false;
                this.$refs.slideA.style.backgroundImage = '';
                this.$refs.slideB.style.backgroundImage = '';
                this.change();
            });
        },
        choose() {
            this.$chooseDriveFolder().then(folder => {
                this.props.folder = folder ? folder.id : null;
                this.save();
                this.fetch();
            });
        }
    }
});
