import Vue from 'vue';
import i18n from '../../../i18n';
import paging from '../../../common/scripts/paging';
export default Vue.extend({
    i18n: i18n('common/views/components/user-list.vue'),
    mixins: [
        paging({}),
    ],
    props: {
        pagination: {
            required: true
        },
        extract: {
            required: false
        },
        iconOnly: {
            type: Boolean,
            default: false
        },
        expanded: {
            type: Boolean,
            default: true
        },
    },
    computed: {
        users() {
            return this.extract ? this.extract(this.items) : this.items;
        }
    }
});
