import Vue from 'vue';
import anime from 'animejs';
export default Vue.extend({
    props: {
        closeOnBgClick: {
            type: Boolean,
            required: false,
            default: true
        },
        openAnimeDuration: {
            type: Number,
            required: false,
            default: 100
        },
        closeAnimeDuration: {
            type: Number,
            required: false,
            default: 100
        }
    },
    mounted() {
        anime({
            targets: this.$refs.bg,
            opacity: 1,
            duration: this.openAnimeDuration,
            easing: 'linear'
        });
    },
    methods: {
        onBgClick() {
            this.$emit('bg-click');
            if (this.closeOnBgClick)
                this.close();
        },
        close() {
            this.$emit('before-close');
            anime({
                targets: this.$refs.bg,
                opacity: 0,
                duration: this.closeAnimeDuration,
                easing: 'linear',
                complete: () => this.destroyDom()
            });
        }
    }
});
