import Vue from 'vue';
import getReactionEmoji from '../../../../../misc/get-reaction-emoji';
export default Vue.extend({
    props: {
        reaction: {
            type: String,
            required: true
        },
        customEmojis: {
            required: false,
            default: () => []
        },
        noStyle: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    computed: {
        _reaction() {
            return getReactionEmoji(this.reaction);
        }
    }
});
