import Vue from 'vue';
export default Vue.extend({
    props: {
        x: {
            type: Number,
            required: true
        },
        y: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            active: false
        };
    },
    mounted() {
        setTimeout(() => {
            this.active = true;
        }, 1);
        setTimeout(() => {
            this.destroyDom();
        }, 1000);
    }
});
