import Vue from 'vue';
import i18n from '../../../i18n';
export default Vue.extend({
    i18n: i18n(),
    props: ['q'],
    data() {
        return {
            query: null
        };
    },
    mounted() {
        this.query = this.q;
    },
    methods: {
        search() {
            const engine = this.$store.state.settings.webSearchEngine ||
                'https://www.google.com/?#q={{query}}';
            const url = engine.replace('{{query}}', this.query);
            window.open(url, '_blank');
        }
    }
});
