import Vue from 'vue';
import * as tinycolor from 'tinycolor2';
export default Vue.extend({
    props: {
        dark: {
            type: Boolean,
            default: false
        },
        smooth: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            now: new Date(),
            enabled: true,
            graduationsPadding: 0.5,
            handsPadding: 1,
            handsTailLength: 0.7,
            hHandLengthRatio: 0.75,
            mHandLengthRatio: 1,
            sHandLengthRatio: 1
        };
    },
    computed: {
        majorGraduationColor() {
            return this.dark ? 'rgba(255, 255, 255, 0.3)' : 'rgba(0, 0, 0, 0.3)';
        },
        minorGraduationColor() {
            return this.dark ? 'rgba(255, 255, 255, 0.2)' : 'rgba(0, 0, 0, 0.2)';
        },
        sHandColor() {
            return this.dark ? 'rgba(255, 255, 255, 0.5)' : 'rgba(0, 0, 0, 0.3)';
        },
        mHandColor() {
            return this.dark ? '#fff' : '#777';
        },
        hHandColor() {
            return tinycolor(getComputedStyle(document.documentElement).getPropertyValue('--primary')).toHexString();
        },
        ms() {
            return this.now.getMilliseconds() * this.smooth;
        },
        s() {
            return this.now.getSeconds();
        },
        m() {
            return this.now.getMinutes();
        },
        h() {
            return this.now.getHours();
        },
        hAngle() {
            return Math.PI * (this.h % 12 + (this.m + (this.s + this.ms / 1000) / 60) / 60) / 6;
        },
        mAngle() {
            return Math.PI * (this.m + (this.s + this.ms / 1000) / 60) / 30;
        },
        sAngle() {
            return Math.PI * (this.s + this.ms / 1000) / 30;
        },
        graduations() {
            const angles = [];
            for (let i = 0; i < 60; i++) {
                const angle = Math.PI * i / 30;
                angles.push(angle);
            }
            return angles;
        }
    },
    mounted() {
        const update = () => {
            if (this.enabled) {
                this.tick();
                requestAnimationFrame(update);
            }
        };
        update();
    },
    beforeDestroy() {
        this.enabled = false;
    },
    methods: {
        tick() {
            this.now = new Date();
        }
    }
});
