import define from '../../../common/define-widget';
import i18n from '../../../i18n';
export default define({
    name: 'rss',
    props: () => ({
        compact: false,
        url: 'http://feeds.afpbb.com/rss/afpbb/afpbbnews'
    })
}).extend({
    i18n: i18n(),
    data() {
        return {
            items: [],
            fetching: true,
            clock: null
        };
    },
    mounted() {
        this.fetch();
        this.clock = setInterval(this.fetch, 60000);
    },
    beforeDestroy() {
        clearInterval(this.clock);
    },
    methods: {
        func() {
            this.props.compact = !this.props.compact;
            this.save();
        },
        fetch() {
            fetch(`https://api.rss2json.com/v1/api.json?rss_url=${this.props.url}`, {}).then(res => {
                res.json().then(feed => {
                    this.items = feed.items;
                    this.fetching = false;
                });
            });
        },
        setting() {
            this.$root.dialog({
                title: 'URL',
                input: {
                    type: 'url',
                    default: this.props.url
                }
            }).then(({ canceled, result: url }) => {
                if (canceled)
                    return;
                this.props.url = url;
                this.save();
                this.fetch();
            });
        }
    }
});
