import Vue from 'vue';
export default Vue.extend({
    props: {
        value: {
            required: false
        },
        required: {
            type: Boolean,
            required: false
        },
        readonly: {
            type: Boolean,
            required: false
        },
        pattern: {
            type: String,
            required: false
        },
        autocomplete: {
            type: String,
            required: false
        },
        tall: {
            type: Boolean,
            required: false,
            default: false
        },
        pre: {
            type: Boolean,
            required: false,
            default: false
        },
    },
    data() {
        return {
            focused: false,
            passwordStrength: ''
        };
    },
    computed: {
        filled() {
            return this.value != '' && this.value != null;
        }
    },
    methods: {
        focus() {
            this.$refs.input.focus();
        }
    }
});
