import Vue from 'vue';
import anime from 'animejs';
import { faTimesCircle, faQuestionCircle } from '@fortawesome/free-regular-svg-icons';
import parseAcct from "../../../../../misc/acct/parse";
import i18n from '../../../i18n';
export default Vue.extend({
    i18n: i18n(),
    props: {
        type: {
            type: String,
            required: false,
            default: 'info'
        },
        title: {
            type: String,
            required: false
        },
        text: {
            type: String,
            required: false
        },
        input: {
            required: false
        },
        select: {
            required: false
        },
        user: {
            required: false
        },
        icon: {
            required: false
        },
        showOkButton: {
            type: Boolean,
            default: true
        },
        showCancelButton: {
            type: Boolean,
            default: false
        },
        cancelableByBgClick: {
            type: Boolean,
            default: true
        },
        splash: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            inputValue: this.input && this.input.default ? this.input.default : null,
            userInputValue: null,
            selectedValue: this.select ? this.select.default ? this.select.default : this.select.items ? this.select.items[0].value : this.select.groupedItems[0].items[0].value : null,
            canOk: true,
            faTimesCircle, faQuestionCircle
        };
    },
    watch: {
        userInputValue() {
            if (this.user) {
                this.$root.api('users/show', parseAcct(this.userInputValue)).then(u => {
                    this.canOk = u != null;
                }).catch(() => {
                    this.canOk = false;
                });
            }
        }
    },
    mounted() {
        if (this.user)
            this.canOk = false;
        this.$nextTick(() => {
            anime({
                targets: this.$refs.main,
                opacity: 1,
                scale: [1.2, 1],
                duration: 300,
                easing: 'cubicBezier(0, 0.5, 0.5, 1)'
            });
            if (this.splash) {
                setTimeout(() => {
                    this.close();
                }, 1000);
            }
        });
    },
    methods: {
        async ok() {
            if (!this.canOk)
                return;
            if (!this.showOkButton)
                return;
            if (this.user) {
                const user = await this.$root.api('users/show', parseAcct(this.userInputValue));
                if (user) {
                    this.$emit('ok', user);
                    this.close();
                }
            }
            else {
                const result = this.input ? this.inputValue :
                    this.select ? this.selectedValue :
                        true;
                this.$emit('ok', result);
                this.close();
            }
        },
        cancel() {
            this.$emit('cancel');
            this.close();
        },
        onBgClick() {
            if (this.cancelableByBgClick)
                this.cancel();
        },
        close() {
            this.$refs.modal.close();
        },
        onBeforeClose() {
            this.$el.style.pointerEvents = 'none';
            this.$refs.main.style.pointerEvents = 'none';
            anime({
                targets: this.$refs.main,
                opacity: 0,
                scale: 0.8,
                duration: 300,
                easing: 'cubicBezier(0, 0.5, 0.5, 1)',
            });
        },
        onInputKeydown(e) {
            if (e.which == 13) { // Enter
                e.preventDefault();
                e.stopPropagation();
                this.ok();
            }
        }
    }
});
