import Vue from 'vue';
export default Vue.extend({
    props: {
        max: {
            type: Number,
            required: false,
            default: undefined
        }
    },
    data() {
        return {
            fetching: true,
            notes: [],
            connection: null
        };
    },
    mounted() {
        this.fetch();
        this.connection = this.$root.stream.useSharedConnection('localTimeline');
        this.connection.on('note', this.onNote);
    },
    beforeDestroy() {
        this.connection.dispose();
    },
    methods: {
        fetch(cb) {
            this.fetching = true;
            this.$root.api('notes', {
                limit: this.max,
                local: true,
                reply: false,
                renote: false,
                file: false,
                poll: false
            }).then(notes => {
                this.notes = notes;
                this.fetching = false;
            });
        },
        onNote(note) {
            if (note.replyId != null)
                return;
            if (note.renoteId != null)
                return;
            if (note.poll != null)
                return;
            if (note.localOnly)
                return;
            this.notes.unshift(note);
        },
    }
});
