import Vue from 'vue';
import debounce from 'v-debounce';
const getPasswordStrength = require('syuilo-password-strength');
export default Vue.extend({
    directives: {
        debounce
    },
    inject: {
        horizonGrouped: {
            default: false
        }
    },
    props: {
        value: {
            required: false
        },
        type: {
            type: String,
            required: false
        },
        required: {
            type: Boolean,
            required: false
        },
        readonly: {
            type: Boolean,
            required: false
        },
        disabled: {
            type: Boolean,
            required: false
        },
        pattern: {
            type: String,
            required: false
        },
        placeholder: {
            type: String,
            required: false
        },
        autofocus: {
            type: Boolean,
            required: false,
            default: false
        },
        autocomplete: {
            required: false
        },
        spellcheck: {
            required: false
        },
        debounce: {
            required: false
        },
        withPasswordMeter: {
            type: Boolean,
            required: false,
            default: false
        },
        withPasswordToggle: {
            type: Boolean,
            required: false,
            default: false
        },
        datalist: {
            type: Array,
            required: false,
        },
        inline: {
            type: Boolean,
            required: false,
            default() {
                return this.horizonGrouped;
            }
        },
        styl: {
            type: String,
            required: false,
            default: 'line'
        }
    },
    data() {
        return {
            v: this.value,
            focused: false,
            invalid: false,
            passwordStrength: '',
            id: Math.random().toString()
        };
    },
    computed: {
        filled() {
            return this.v != '' && this.v != null;
        },
        filePlaceholder() {
            if (this.type != 'file')
                return null;
            if (this.v == null)
                return null;
            if (typeof this.v == 'string')
                return this.v;
            if (Array.isArray(this.v)) {
                return this.v.map(file => file.name).join(', ');
            }
            else {
                return this.v.name;
            }
        }
    },
    watch: {
        value(v) {
            this.v = v;
        },
        v(v) {
            if (this.type === 'number') {
                this.$emit('input', parseInt(v, 10));
            }
            else {
                this.$emit('input', v);
            }
            if (this.withPasswordMeter) {
                if (v == '') {
                    this.passwordStrength = '';
                    return;
                }
                const strength = getPasswordStrength(v);
                this.passwordStrength = strength > 0.7 ? 'high' : strength > 0.3 ? 'medium' : 'low';
                this.$refs.passwordMetar.style.width = `${strength * 100}%`;
            }
            this.invalid = this.$refs.input.validity.badInput;
        }
    },
    mounted() {
        if (this.autofocus) {
            this.$nextTick(() => {
                this.$refs.input.focus();
            });
        }
        this.$nextTick(() => {
            // このコンポーネントが作成された時、非表示状態である場合がある
            // 非表示状態だと要素の幅などは0になってしまうので、定期的に計算する
            const clock = setInterval(() => {
                if (this.$refs.prefix) {
                    this.$refs.label.style.left = (this.$refs.prefix.offsetLeft + this.$refs.prefix.offsetWidth) + 'px';
                    if (this.$refs.prefix.offsetWidth) {
                        this.$refs.input.style.paddingLeft = this.$refs.prefix.offsetWidth + 'px';
                    }
                }
                if (this.$refs.suffix) {
                    if (this.$refs.suffix.offsetWidth) {
                        this.$refs.input.style.paddingRight = this.$refs.suffix.offsetWidth + 'px';
                    }
                }
            }, 100);
            this.$once('hook:beforeDestroy', () => {
                clearInterval(clock);
            });
        });
        this.$on('keydown', (e) => {
            if (e.code == 'Enter') {
                this.$emit('enter');
            }
        });
    },
    methods: {
        focus() {
            this.$refs.input.focus();
        },
        togglePassword() {
            if (this.type == 'password') {
                this.type = 'text';
            }
            else {
                this.type = 'password';
            }
        },
        chooseFile() {
            this.$refs.file.click();
        },
        onChangeFile() {
            this.v = Array.from(this.$refs.file.files);
            this.$emit('input', this.v);
            this.$emit('change', this.v);
        }
    }
});
