import Vue from 'vue';
export default Vue.extend({
    inject: {
        horizonGrouped: {
            default: false
        }
    },
    props: {
        value: {
            required: false
        },
        required: {
            type: Boolean,
            required: false
        },
        disabled: {
            type: Boolean,
            required: false
        },
        styl: {
            type: String,
            required: false,
            default: 'line'
        },
        inline: {
            type: Boolean,
            required: false,
            default() {
                return this.horizonGrouped;
            }
        },
    },
    data() {
        return {
            focused: false
        };
    },
    computed: {
        v: {
            get() {
                return this.value;
            },
            set(v) {
                this.$emit('input', v);
            }
        },
        filled() {
            return this.v != '' && this.v != null;
        }
    },
    mounted() {
        if (this.$refs.prefix) {
            this.$refs.label.style.left = (this.$refs.prefix.offsetLeft + this.$refs.prefix.offsetWidth) + 'px';
        }
    },
    methods: {
        focus() {
            this.$refs.input.focus();
        }
    }
});
