import Vue from 'vue';
import i18n from '../../../i18n';
export default Vue.extend({
    i18n: i18n('common/views/components/instance.vue'),
    data() {
        return {
            meta: null
        };
    },
    created() {
        this.$root.getMeta().then(meta => {
            this.meta = meta;
        });
    }
});
