import Vue from 'vue';
import i18n from '../../../i18n';
import { toUnicode } from 'punycode';
import { host as localHost } from '../../../config';
export default Vue.extend({
    i18n: i18n(),
    props: {
        username: {
            type: String,
            required: true
        },
        host: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            localHost
        };
    },
    computed: {
        url() {
            switch (this.host) {
                case 'twitter.com':
                case 'github.com':
                    return `https://${this.host}/${this.username}`;
                default:
                    return `/${this.canonical}`;
            }
        },
        canonical() {
            return this.host === localHost ? `@${this.username}` : `@${this.username}@${toUnicode(this.host)}`;
        },
        isMe() {
            return this.$store.getters.isSignedIn && (`@${this.username}@${toUnicode(this.host)}` === `@${this.$store.state.i.username}@${toUnicode(localHost)}`.toLowerCase());
        }
    },
    methods: {
        toUnicode
    }
});
